.movie-view {
    background-color: aqua;
    padding-top: 50px;
  }
  
  .movie-info {
    padding: 40px !important;
    background-color: rgb(237, 237, 237);
  }
  .movie-image {
    padding: 0 !important;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: blueviolet;
  }
  .poster-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .back-button {
    margin-top: 50px;
    padding: 4px 12px 4px 12px;
    color: whitesmoke;
    background-color: dodgerblue;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  .custom-link-button {
    padding-left: 0 !important;
  }